import {IconCategory} from '@here/hds-iconlibrary';
import React, {useEffect, useImperativeHandle} from 'react';
import {RichListItem, RichListItemState} from '@here/hds-rich-list';
import {HDSListItemProps} from '../List/HDSListItem.js';

// eslint-disable-next-line no-new
new RichListItem();

interface HDSRichListItemProperties extends HDSListItemProps {
  disabled?: boolean;
  state?: RichListItemState;
  headline?: string;
  subTitle?: string;
  progressText?: string;
  progress?: number;
  progressVariant?: string;
  errorText?: string;
  filePreview?: boolean;
  iconDefault?: string;
  iconDefaultCategory?: IconCategory;
  iconProgress?: string;
  iconProgressCategory?: IconCategory;
  iconError?: string;
  iconErrorCategory?: IconCategory;
  iconSuccess?: string;
  iconSuccessCategory?: IconCategory;
  hideIcon?: boolean;
  hideCancel?: boolean;
  hideRetry?: boolean;
  hideDelete?: boolean;
  actions?: React.ReactNode;
  file?: File;
}

const HDSRichListItem = React.forwardRef((props: HDSRichListItemProperties, ref) => {
  const {
    children,
    disabled,
    state,
    headline,
    subTitle,
    progressText,
    progress,
    errorText,
    filePreview,
    iconDefault,
    iconDefaultCategory,
    iconProgress,
    iconProgressCategory,
    iconError,
    iconErrorCategory,
    iconSuccess,
    iconSuccessCategory,
    hideIcon,
    hideCancel,
    hideRetry,
    hideDelete,
    actions,
    file,
    className,
    progressVariant,
    ...rest
  } = props;

  const innerRef = React.useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  useEffect(() => {
    if (innerRef.current) {
      (innerRef.current as RichListItem).file = file;
    }
  }, [file, innerRef]);

  const attributes: HDSRichListItemProperties = {};
  subTitle && (attributes['sub-title'] = subTitle);
  progressText && (attributes['progress-text'] = progressText);
  errorText && (attributes['error-text'] = errorText);
  filePreview && (attributes['file-preview'] = true);
  iconDefault && (attributes['icon-default'] = iconDefault);
  iconDefaultCategory && (attributes['icon-default-category'] = iconDefaultCategory);
  iconProgress && (attributes['icon-progress'] = iconProgress);
  iconProgressCategory && (attributes['icon-progress-category'] = iconProgressCategory);
  iconError && (attributes['icon-error'] = iconError);
  iconErrorCategory && (attributes['icon-error-category'] = iconErrorCategory);
  iconSuccess && (attributes['icon-success'] = iconSuccess);
  iconSuccessCategory && (attributes['icon-success-category'] = iconSuccessCategory);
  hideIcon && (attributes['hide-icon'] = false);
  hideCancel && (attributes['hide-cancel'] = false);
  hideRetry && (attributes['hide-retry'] = false);
  hideDelete && (attributes['hide-delete'] = false);
  progressVariant && (attributes['progress-variant'] = progressVariant);

  return (
    <hds-rich-list-item
      {...attributes}
      progress={progress}
      state={state}
      headline={headline}
      ref={innerRef}
      {...(disabled && {disabled})}
      {...(className && {class: className})}
      {...rest}
    >
      {children}
      {actions}
    </hds-rich-list-item>
  );
});

HDSRichListItem.defaultProps = {
  disabled: false,
  state: 'default',
  headline: '',
  subTitle: '',
  progressText: '',
  progress: 0,
  progressVariant: 'determinate',
  errorText: '',
  filePreview: false,
  iconDefault: 'time',
  iconDefaultCategory: 'core-ui',
  iconProgress: 'time',
  iconProgressCategory: 'core-ui',
  iconError: 'alert',
  iconErrorCategory: 'core-ui',
  iconSuccess: 'checkmark',
  iconSuccessCategory: 'core-ui',
  hideIcon: false,
  hideCancel: false,
  hideRetry: false,
  hideDelete: false,
};

export default HDSRichListItem;
